import { Text, FlatList, Image, LayoutAnimation } from "react-native";

import { Screen } from "../components/Screen";
import { useCallback, useContext, useEffect, useMemo, useState } from "react";
import { AddressContext } from "../App";
import { convertToHumanReadable, getBaseUrl, runIfFunction, toLocaleDecimal } from "../utils/common";
import { UIManager, View, TouchableOpacity, StyleSheet } from 'react-native';
import { MaterialCommunityIcons } from "@expo/vector-icons";
import { Hunt, newHunt } from "../helpers/api";
import { LinearGradient } from "expo-linear-gradient";
import moment from 'moment';

const badge_bronze = require('../../assets/rank/bronze.png');
const badge_silver = require('../../assets/rank/silver.png');
const badge_gold = require('../../assets/rank/gold.png');
const badge_platinum = require('../../assets/rank/platinum.png');
const badge_diamond = require('../../assets/rank/diamond.png');
const badge_master = require('../../assets/rank/master.png');
const COOLDOWN = 60; // 60s

// hard code some ranks
const ranks = {
  bronze: 0,
  silver: 200,
  gold: 1000,
  platinum: 5000,
  diamond: 10000,
  master: 50000,
}

// allow animation
// UIManager.setLayoutAnimationEnabledExperimental && UIManager.setLayoutAnimationEnabledExperimental(true);

const LootText = ({ item }: { item: Hunt }) => {
  let hasLoot = item.hunt_loots.length > 0;

  if(!hasLoot) {
    return null;
  }

  return (<Text>
    {" Furthermore, the monster dropped "}
    {
      item.hunt_loots.map((loot, index) => {
        let prepend = ",";
        if(index === 0) {
          prepend = "";
        }
  
        else if(index >= 2 && index === item.hunt_loots.length - 1) {
          prepend = ", and "
        }
  
        else if(index === item.hunt_loots.length - 1) {
          prepend = " and "
        }

        return <Text key={`loot_text_${item.id}_${index}`}>{prepend}<Text style={{fontWeight: 'bold'}}>1x {loot.loot![0].name}</Text></Text>;
      })
    }
    {"."}
  </Text>)
    
}

export function HomeScreen() {
  const [ isLoading, setIsLoading ] = useState(true);
  const [ isOnCooldown, setIsOnCooldown ] = useState(true);
  const [ isWaitingForResult, setIsWaitingForResult ] = useState(false);
  const [ isAddressVisible, setIsAddressVisible ] = useState(false);
  const [ cd, setCd ] = useState(0);
  const addressContext = useContext(AddressContext);

  /* const setAnimation = () => {
    LayoutAnimation.configureNext({
      duration: 250,
      update: {
        type: LayoutAnimation.Types.easeIn,
        springDamping: 0.7,
      },
    });
    LayoutAnimation.configureNext({
      duration: 500,
      update: {
        type: LayoutAnimation.Types.easeIn,
        property: LayoutAnimation.Properties.scaleXY,
        springDamping: 0.7,
      },
    });
  } */

  const startCountDown = useCallback((cd: number) => {
    setCd(cd);
    let interval = setInterval(() => {
      cd--;
      // precaution
      if(cd < 0) {
        setIsOnCooldown(false);
        clearInterval(interval);
        return;
      }

      setCd(cd);
      
      if(cd === 0) {
        setIsOnCooldown(false);
        clearInterval(interval);
        return;
      }
    }, 1000);
  }, []);

  useEffect(() => {
    // setAnimation();
    if(addressContext.history.length === 0){
      setIsLoading(false);
      setIsOnCooldown(false);
      return;
    }

    // only load this the first time
    if(!isLoading) {
      return;
    }

    let lastHuntSecondsAgo = moment().diff(moment(addressContext.history[0].created_at), 's');
    if(lastHuntSecondsAgo < COOLDOWN) {
      setIsLoading(false);
      setIsOnCooldown(true);
      setCd(COOLDOWN - lastHuntSecondsAgo);
      startCountDown(COOLDOWN - lastHuntSecondsAgo);
      return;
    }

    setIsLoading(false);
    setIsOnCooldown(false);
  }, [isLoading, addressContext.history]);

  const badge = useMemo(() => {
    const { tokens: { exp } } = addressContext;
    if(exp >= ranks.master) {
      return badge_master;
    }
    if(exp >= ranks.diamond) {
      return badge_diamond;
    }
    if(exp >= ranks.platinum) {
      return badge_platinum;
    }
    if(exp >= ranks.gold) {
      return badge_gold;
    }
    if(exp >= ranks.silver) {
      return badge_silver;
    }
    return badge_bronze;
  }, [addressContext]);

  return (
    <Screen>
      <View style={{
        position: 'absolute',
        top: 0,
        left: 0,
        right: 0,
        height: '100vh',
      }}>
        <Image
          source={{ uri: getBaseUrl() + '/assets/bg_blur/grasslands_bg.png' }}
          style={{ height: '100%' }}
        />
      </View>
      <View style={{ marginBottom: 20 }}>
        <View style={{
          height: 70,
          width: 70,
          borderRadius: 50,
          backgroundColor: 'white',
          borderColor: '#8a4d0f',
          borderWidth: 1,
          zIndex: 2,
          alignItems: 'center',
          justifyContent: 'center',
        }}>
          {/** Badge here */}
          <Image
            source={badge}
            style={{
              width: 30,
              height: 33
            }}
          />
        </View>
            <View style={[styles.playerTokens, { left: 35, top: 18 }]}>
              {/** diagonal shape */}
              <View style={styles.triangleCorner} />
              <View style={styles.triangleCornerInner} />
              <Text style={{ fontSize: 10 }}>{toLocaleDecimal(addressContext.tokens.gold, 2, 2)} Gold</Text>
            </View>
            <View style={[styles.playerTokens, { left: 15, top: 33, paddingLeft: 60 }]}>
              <View style={styles.triangleCorner} />
              <View style={styles.triangleCornerInner} />
              <Text style={{ fontSize: 10 }}>
                {toLocaleDecimal(addressContext.tokens.exp, 2, 2)} EXP
              </Text>
              {/** Progress bar here */}
            </View>
      </View>

      {/** Wallet Container */}
      <View style={{ position: 'absolute', top: 60, right: 10, alignItems: 'center', justifyContent: 'center', flexDirection: 'row' }}>
        {
          isAddressVisible?
          <Text style={{ marginRight: 5, fontSize: 8 }}>{addressContext.account}</Text> :
          null
        }
        <TouchableOpacity
          onPress={() => setIsAddressVisible(!isAddressVisible)}
        >
          <View 
            style={{ height: 50, alignItems: 'center', justifyContent: 'center' }}
          >
            <MaterialCommunityIcons name="wallet" size={20} color="black"/>
          </View>

      </TouchableOpacity>

      </View>

      {/** Hunt container */}
      <View style={{
        flex: 1,
        minHeight: '90%',
        overflow: 'hidden',
        backgroundColor: '#fffef0',
        padding: 20,
      }}>
        {/** Inner shadows */}
        <View 
          style={{
            position: 'absolute',
            top: -5,
            left: 0,
            right: 0,
            shadowColor: '#8a4d0f',
            shadowRadius: 15,
            shadowOpacity: 1,
            height: 5,
            backgroundColor: '#fffef0',
          }}
        >
        </View>
        <View 
          style={{
            position: 'absolute',
            bottom: -5,
            left: 0,
            right: 0,
            shadowColor: '#8a4d0f',
            shadowRadius: 15,
            shadowOpacity: 1,
            height: 5,
            backgroundColor: '#fffef0',
          }}
        >
        </View>
        <View 
          style={{
            position: 'absolute',
            top: 0,
            left: -5,
            bottom: 0,
            shadowColor: '#8a4d0f',
            shadowRadius: 15,
            shadowOpacity: 1,
            width: 5,
            backgroundColor: '#fffef0',
          }}
        >
        </View>
        <View 
          style={{
            position: 'absolute',
            top: 0,
            bottom: 0,
            right: -5,
            shadowColor: '#8a4d0f',
            shadowRadius: 10,
            shadowOpacity: 1,
            width: 5,
            backgroundColor: '#fffef0',
          }}
        >
        </View>
        <Image
          style={{ position: 'absolute', top: 0, left: 0, right: 0, bottom: 0 }}
          source={{uri: "data:image/png;base64,"}}
        />
        
        {/** Hunting horn here */}
        <TouchableOpacity 
          onPress={async() => {
            setIsOnCooldown(true);
            startCountDown(COOLDOWN);
            setIsWaitingForResult(true);

            // use back the keyed in account for simplicity since every backend logic is handled by this
            await newHunt({ 
                              account: addressContext.isPublicKey? addressContext.account : addressContext.inputAccount,
                              isPublicKey: addressContext.isPublicKey 
                          });

            // get data after 2 seconds cause sometimes the rpcs have not yet indexed the transaction
            setTimeout(async() => {
              await runIfFunction(addressContext.getData);
              setIsWaitingForResult(false);
            }, 2000);
            // setAnimation();
          }}
          accessibilityRole="button"
          disabled={isOnCooldown || isLoading}
        >
          <LinearGradient
            colors={['#ab875e', 'transparent', '#ab875e',]}
            start={{ x: 0, y: 0 }}
            end={{ x: 1, y: 0 }}
            style={{ 
              width: '100%', 
              height: '100%', 
              flexDirection: 'row', 
              alignItems: 'center',
              borderRadius: 5,
            }}
          >
            <View style={{ 
              height: 40,
              flexDirection: 'row',
              alignItems: 'center',
              justifyContent: 'center',
              borderRadius: 5,
              shadowColor: 'black',
              shadowRadius: 3,
              flex: 1,
            }}>
              {
                isOnCooldown && !isLoading?
                <>
                <Text style={{ letterSpacing: 5, marginLeft: 25, marginRight: 20, color: "#4d4235", fontWeight: 'bold' }}>{cd}</Text>
                </>:
                <>
                <MaterialCommunityIcons name="sword-cross" color='#4d4235' size={20} />
                <Text style={{ letterSpacing: 5, marginLeft: 25, marginRight: 20, color: "#4d4235" }}>HUNT</Text>
                <MaterialCommunityIcons name="sword-cross" color='#4d4235' size={20} />
                </>
              }
            </View>
          </LinearGradient>
        </TouchableOpacity>
        
        {/** Hunting log here */}
        {
          addressContext.history.length === 0 && !isWaitingForResult?
          <View style={{ marginTop: 10, alignItems: 'center', justifyContent: 'center'}}>
            <Text style={{fontWeight: 'bold', marginBottom: 10}}></Text>
            <Text style={{fontWeight: 'bold'}}>Click the Hunt Button to start a Hunt!</Text>
          </View>
           :
          null
        }

        {
          isWaitingForResult?
          <View style={{
            marginTop: 20,
            marginBottom: 5,
            width: '100%',
            alignItems: 'center',
            justifyContent: 'center',
            height: 120,
            borderWidth: 1,
            borderRadius: 5,
          }}>
            <Text style={{ fontWeight: 'bold', fontSize: 15 }}>Hunting..</Text>
          </View> :
          null
        }

        <FlatList
          style={{ marginTop: isWaitingForResult? 0 : 20, paddingTop: 30, paddingBottom: 100 }}
          data={addressContext.history}
          keyExtractor={(item) => `hunt_${item.id}`}
          showsVerticalScrollIndicator={false}
          
          renderItem={({ item, index }) => {
            
            return (<View style={{
              marginBottom: 35,
              width: '100%',
            }}>
              <Text style={{ fontSize: 8 }}>{convertToHumanReadable(item.created_at)}</Text>
              <View style={{
                flexDirection: index % 2 === 1? 'row' : 'row-reverse',
                alignItems: 'center',
                marginTop: 5,
                paddingVertical: 10,
              }}>
                <Text style={{ fontSize: 11 }}>{item.caught? 'Successfully captured' : 'Encountered'} a <Text style={{fontWeight: 'bold'}}>{item.is_shiny? '*shiny* ' : ''}{item.monster.name}</Text> worth <Text style={{fontWeight: 'bold'}}>{item.gold}</Text> gold and <Text style={{fontWeight: 'bold'}}>{item.exp}</Text> exp{item.caught? '.' : ' but we failed to capture it.'}<LootText item={item}/></Text>
                <Image
                  source={{ uri: `${getBaseUrl()}/assets/sprites/base${item.is_shiny? '_shiny' : ''}/${item.monster.img_file}` }}
                  style={{ 
                    height: 80, 
                    width: 80, 
                    marginLeft: index % 2 === 1? 10 : 0, 
                    marginRight: index % 2 === 1? 0 : 10,  
                    resizeMode: 'contain'
                  }}
                />
              </View>
            </View>)
          }}
        />
      </View>
    </Screen>
  );
}

const styles = StyleSheet.create({
  playerTokens: {
    position: 'absolute',
    height: 15,
    width: 165,
    borderRadius: 3,
    // borderColor: 'black',
    // borderWidth: 1,
    paddingLeft: 40,
    zIndex: 1,
    alignItems: 'flex-start',
    justifyContent: 'center'
  },
  triangleCorner: {
    position: 'absolute',
    top: 0,
    bottom: 0,
    left: 0,
    right: 0,
    borderTopWidth: 15,
    borderRightWidth: 10,
    borderRightColor: 'transparent',
    borderTopColor: 'gray',
    zIndex: -2
  },
  triangleCornerInner: {
    position: 'absolute',
    top: 1,
    bottom: 1,
    left: 1,
    right: 1,
    borderTopWidth: 14,
    borderRightWidth: 9,
    borderRightColor: 'transparent',
    borderTopColor: 'white',
    zIndex: -2
  }
});